import React from 'react';
import ReactDOM from 'react-dom';
import SubmitDataHelpers from '../helpers/SubmitDataHelpers';

import EventListStyle from '../../css/events/EventListStyle';
import '../../css/Layout/Main.css';
import '../../css/events/EventList.css';


class EventList extends React.Component{
    constructor(props){
        let x = super(props);

        this.state = {
            searchEvents: [],
            myEvents: [],
        };

        this.searchEvents = this.searchEvents.bind(this);
    }

    componentDidMount(){
        if(window.location.hash.replace('/#/', '/') !== '/events/'){
            window.location.href = window.sessionStorage.path + 'events/';

            return false;
        }
        
        let date = new Date();
        let today = date.getFullYear() + '-' + (date.getMonth()<9? '0' + (date.getMonth()+1): date.getMonth() + 1) + '-' + (date.getDate()<9? '0' + date.getDate(): date.getDate());
        
        let helpers = new SubmitDataHelpers;
        helpers.postData('/events/index/', {})
        .then(res => {
            if(!res){
                //console.log("no res");
                return false;
            }

            if(!res.error && window.location.hash.replace('/#/', '/') === '/events/'){
                //console.log(res);
                let myEvents = [];
                
                res.forEach(event => {
                    if(event.date > today){
                        event.status = "Coming..."
                    } else if (event.date == today){
                        event.status = "Opening...";
                    } else {
                        event.status = "Closed...";
                    }

                    myEvents.push(event);
                })

                this.setState({myEvents: myEvents});
            } else {
                //console.log(res);
                //showAlert(res.error, 5000);

                this.setState({error: res.error});
            }
        });

        EventListStyle();
    }

    componentDidUpdate(){
        EventListStyle();
    }

    render(){
        let events;
        if(this.state.searchEvents.length == 0){
            events = this.state.myEvents;
        } else{
            events = this.state.searchEvents;
        }

        let tbody = [];
        events.forEach(event => {
            tbody.push(
                <div className="tr row py-2" value={event.name} key={event.name} onClick={() => this.selectEvent(event.name, event.date).then(res=>{})}>
                    <div className="td col-3" 
                        data-toggle="tooltip" data-placement="top" title={window.innerWidth > 567 ? '' : event.name}
                    >{window.innerWidth > 567 ? event.name : event.name.substring(0,5) + '...'}</div>
                    <div className="td col-3">{window.innerWidth > 567 ? event.date : event.date.substring(5).replace(/-/g,'/')}</div>
                    <div className="td col-3">{window.innerWidth > 567 ? event.location : event.location.substring(0,5) + '...'}</div>
                    <div className="td col-3">{event.status.replace('...','')}</div>
                </div>
            )
        });
        
        return(
            <div id="events" className="events mx-auto pt-3">
                <div className='title pt-5 pb-3'>
                    <h2>My Events</h2>
                    <h6>( {events.length < 10? '0' + events.length: events.length} events )</h6>
                </div>

                <div className="search input-group mb-4">
                    <div className="input-group-prepend">
                        <span className="input-group-text">
                            <i className="fas fa-search"></i>
                        </span>
                    </div>
                    <input className="form-control"
                        type="text" name="searchInfo"
                        autoComplete = "off"
                        onChange={this.searchEvents} placeholder="Search events..." 
                    />
                    <div className=" input-group-append" data-toggle="tooltip" data-placement="top" title="Search from all events...">
                        <button className="allEvents input-group-text custom-control custom-checkbox">
                            <input id="allEvents" className="custom-control-input" type="checkbox" name="allEvents" value="true" defaultChecked/>
                            <label className="custom-control-label" htmlFor="allEvents"></label>
                        </button>
                    </div>
                </div>

                <div className="eventTable mx-auto shadow p-2 border">
                    <div className="table mx-auto border">
                        <div className="thead col-12">
                            <div className="tr row py-3">
                                <div className="th col-3">Name</div>
                                <div className="th col-3">Date</div>
                                <div className="th col-3">Location</div>
                                <div className="th col-3">Status</div>
                            </div>
                        </div>
                        <div className="tbody col-12 overflow-auto">
                            {tbody.length > 0? tbody: 'No events'}
                        </div>
                    </div>
                </div>

                <div className="addEvent rounded-circle shadow" data-toggle="tooltip" data-placement="top" title="Add event...">
                    <button className=" addEventBtn rounded-circle p-3 border" onClick={this.addEvent}>
                        <i className="fas fa-plus"></i>
                    </button>
                </div>
            </div>
        )
    }

    searchEvents(e){
        let searchInfo = e.target.value.toLowerCase();
        let allEvents = document.getElementById('allEvents').checked;
        if(searchInfo == ''){
            this.setState({searchEvents: []});
            return false;
        }

        if(allEvents){
            let date = new Date();
            let today = date.getFullYear() + '-' + (date.getMonth()<9? '0' + (date.getMonth()+1): date.getMonth() + 1) + '-' + (date.getDate()<9? '0' + date.getDate(): date.getDate());            
            let helpers = new SubmitDataHelpers;
            helpers.postData('/events/searchEvents/', {
                searchInfo: searchInfo,
            })
            .then(res => {
                if(!res.error && window.location.hash.replace('/#/', '/') === '/events/'){
                    let searchEvents = [];
                    
                    res.forEach(event => {
                        if(event.date > today){
                            event.status = "Coming..."
                        } else if (event.date == today){
                            event.status = "Opening...";
                        } else {
                            event.status = "Closed...";
                        }

                        searchEvents.push(event);
                    })

                    this.setState({searchEvents: searchEvents});
                } else {
                    //console.log(res);
                    let searchEvents = [];

                    searchEvents.push({
                                        name: "N/A",
                                        date: "N/A",
                                        location: "N/A",
                                        status: "N/A",
                                    });
                                    
                    this.setState({searchEvents: searchEvents});
                }
            });
        } else {
            let myEvents = this.state.myEvents.slice();
            let searchEvents = [];

            myEvents.forEach(event => {
                let match = event.name.toLowerCase().includes(searchInfo) 
                        || event.date.toLowerCase().includes(searchInfo)
                        || event.location.toLowerCase().includes(searchInfo)
                        || event.status.toLowerCase().includes(searchInfo);

                if(match){
                    searchEvents.push(event);
                }
            });

            if(searchEvents.length == 0){
                searchEvents.push({
                    name: "N/A",
                    date: "N/A",
                    location: "N/A",
                    status: "N/A",
                });
            }
            //console.log(searchEvents);
            this.setState({searchEvents: searchEvents});
        }
    }

    async selectEvent(eventName, eventDate){
        if(document.getElementById("popUpModal")){
            document.getElementById("popUpModal").remove();
        }

        let helpers = new SubmitDataHelpers;
 
        return helpers.postData('/events/checkCreator/',{
            eventName: eventName.toLowerCase(),
        }).then(res => {        
            if(res.url){ 
                window.sessionStorage.setItem("eventName", eventName);
                window.location.href = window.sessionStorage.path + res.url;
            } else if(res.isPlayer){
                this.renderPlayerAccess(eventName, eventDate);
            }
        });
    }

    async renderPlayerAccess(eventName, eventDate){
        let module = await import(`./PlayerAccess`);
        const PlayerAccess = await module.default;

        let modal = document.createElement("div");
        modal.id = "popUpModal";
        modal.className = "popUpModal";

        if(window.localStorage.getItem('darkThemeMode') == 'on'){
            modal.className += " dark-popUpModal";   
        }

        document.body.appendChild(modal);
        ReactDOM.render(<PlayerAccess eventName={eventName} eventDate={eventDate}/>, document.getElementById('popUpModal'));
    }

    async addEvent(){
        let module = await import(`./CreateEvent`);
        const CreateEvent = await module.default;

        let modal = document.createElement("div");
        modal.id = "popUpModal";
        modal.className = "popUpModal";

        if(window.localStorage.getItem('darkThemeMode') == 'on'){
            modal.className += " dark-popUpModal";   
        }

        document.body.appendChild(modal);
        ReactDOM.render(<CreateEvent />, document.getElementById('popUpModal'));
    }
}

export default EventList;